@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
.App {
  text-align: center;
}

.timer {
  font-size: 1.2em;
  font-weight: bold;
  justify-content: center;
}
.App-header {
  background-color: #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 25px;
  color: white;
  height: 50px;
  width: auto;
}

.paper {
  display: "flex";
  justify-content: "center";
  text-align: center;
  background-color: rgb(255, 255, 255);
  border: "2px solid #000";
  color: black;
  padding: 10px;
}

.buttonBox {
  justify-content: center;
  text-align: center;
  border-style: groove;
  border-width: 3px;
  width: 380px;
  border-radius: 25px;
  padding: 10px;
  margin: 20px;

  background-color: rgb(200, 249, 255);
  box-shadow: 5px 5px 5px rgb(32, 32, 32);
}

.timegrid {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: center;
}

.btn {
  border-radius: 25px;
}

.btn.btn-custom-lg,
.btn.btn-custom-sm,
.btn.btn-custom-xs {
  border-radius: 50px;
  border: black;
  border-width: 1px;
  box-shadow: 5px 5px 5px rgb(32, 32, 32);
}

.pinkbackground {
  background-color: rgb(250, 203, 211);
}
.redbackground {
  background-color: rgb(255, 37, 37);
}
.greenbackground {
  background-color: lightgreen;
}
.orangebackground {
  background-color: rgb(255, 169, 8);
}
.yellowbackground {
  background-color: yellow;
}
.bluebackground {
  background-color: rgb(67, 67, 255);
}
.tealbackground {
  background-color: rgb(108, 255, 157);
}

.left {
  margin-left: 15px;
}
html {
  scroll-behavior: smooth;
}

.spacemaker {
  margin-right: 20px;
}
.addbutton {
  background-color: rgb(196, 248, 255);
  margin-top: 6px;
  border-radius: 25px;
  padding: 5px;
  color: black;
}
.btn.btn-custom-lg,
.btn.btn-custom-sm,
.btn.btn-custom-xs {
  border-radius: 50px;
}
.addAday__link,
.addAday__link:hover,
.addAday__link:active {
  text-decoration: none;
  color: white;
}

.add-form {
  margin-bottom: 40px;
  border-radius: 25px;
}

